/* Variable declarations */
:root{
    --mainBlue: #006699;
    --mainGreen: #339966;
    --darkGray: #403B41;
    --softGray: #DEDEDE;
    --serif: 'David Libre', serif;
    --sansSerif: 'Arimo', sans-serif;
}

/* Overall styles - Starting points */
html {
    font-size: 16pt;
    background-color: var(--darkGray);
    color: var(--softGray);
    font: var(--sansSerif);
}

/* Hero section */
.hero {
    border: 5px solid var(--mainBlue);
    padding: 3%;
    margin-bottom: 5%;
}

.hero h1 {
    text-align: center;
    font: 4.25em var(--serif);
}

.intro-icons {
    text-align: center;
    width: 75%;
    margin: 0 auto;
}

.intro-icons img{
    width: 30%;
    padding: 0 4vw;
    color: var(--darkGray);
}

/* Project Styles */
.project-block {
    width: 97vw;
    border: 3px solid var(--mainGreen);
    margin: 1% 0;
    display: grid;
    grid-template-rows: 20% 30% 20% 30%;
    grid-template-columns: 40% 30% 30%;
  }
  
  .project-images {
    grid-column: 1/2;
    grid-row: 1 / span 4;
  }
  
  .project-images img {
    width: 100%;
    height: 100%;
  }
  
  .project-name {
    grid-column: 2 / 4;
    grid-row: 1;
    justify-self: center;
    font-size: 1.5em;
  }
  
  .project-desc {
    grid-column: 2 / 4;
    grid-row: 2;
    padding: 0 5%;
  }
  
  .project-tech {
    grid-column: 2 / 4;
    grid-row: 3;
    justify-self: center;
    color: lightblue;
  }
  
  .project-block a {
    grid-row: 4;
    padding: 4%;
    color: var(--softGray);
  }
  
  .project-site {
    grid-column: 2;
    justify-self: center;
  }
  
  .project-code {
    grid-column: 3;
    justify-self: center;
  }
  
  @media screen and (max-width: 500px) {
    .project-block {
      width: 97vw;
      border: 3px solid red;
      margin: 1% 0;
      display: grid;
      grid-template-columns: 50% 50%;
      grid-template-rows: 30% 20% 30% 10% 10%;
    }

    .project-images {
        grid-column: 1/ span 2;
        grid-row: 1 / 2;
      }

    .project-name {
        grid-column: 1 / span 2;
        grid-row: 2;
        justify-self: center;
        font-size: 1.5em;
    }
    
    .project-desc {
        grid-column: 1 / span 2;
        grid-row: 3;
        padding: 0 5%;
    }
    
    .project-tech {
        grid-column: 1 / span 2;
        grid-row: 4;
        justify-self: center;
        color: lightblue;
    }
    
    .project-block a {
        grid-row: 5;
        padding: 4%;
    }
    
    .project-site {
        grid-column: 1;
        justify-self: center;
    }
    
    .project-code {
        grid-column: 2;
        justify-self: center;
    }
  }